import code from "../../util/code.js"
import common from "../../util/common.js"
export default {
	data() {
		return {
			key:'',
			status:null,
			linkName:''
		};
	},
	onLoad(option) {
		const islogin = this.$islogin.isLogin;
		const that = this;
		const user = uni.getStorageSync('userInfo');
		if(!user.uuid && !islogin){
			uni.showToast({
				title: '请登录',
				icon: 'none',
				duration: 2000
			});
			setTimeout(function(){
				uni.navigateTo({
					url: '/pages/index/index'
				});
			},2000)
			return false;
		}
		that.key = option.key;
		//获取招聘信息
		uni.request({
			url: common.port.recruitmentById + '/'+option.key,
			method: "GET",
			dataType: "json",
			data: {},
			success: (result) => {
				if (result.data.code == 200) {
					that.status = result.data.data.applyStatus;
					that.linkName = result.data.data.recruitTheme;
				}else {
					uni.showToast({
						title: result.data.msg,
						icon: 'none',
						duration: 2000,
					})
				}
			}
		})
	},
	methods:{
		back:function(e){
			uni.navigateBack({
			    delta: 1
			});
		}
	}
}
